         <div class="tongzhi">
              <div class="waitTitle">正在通知驻场人员请耐心等候……</div>
              <div class="tongzhifont">已通知 <span class="num">{{num}}</span>人</div>
              <el-button @click="cancel" type="warning">取消抢单</el-button>
              <div class="waitTime">已等候{{miao}}秒</div>
         </div>   
    </div>  
    
