<script>
const queryString = require("querystring");
import UserAddress from "components/address";
import { ScondDay } from "utils/formData";
import { FileChange } from "utils/formData";
export default {
  data() {
    return {
      num: this.$route.query.num,
      miao:1
    };
  },
  mounted() {
    this.getMiao();
  },
  methods: {
    getMiao(){
      let that=this;
      setInterval(function(){
      that.miao=that.miao+1;
      that.axios
        .get("/productOrder/" + that.$route.query.mainId)
        .then(res => {
          if (res.data.code === 200) {
             if(res.data.data.groundPersonId!=null){
                 this.$router.push({
                  path: "orderMain",
                  query: {
                    id: that.$route.query.mainId,
                     stateValue: "已派发",
                    childStateValue: "未转单",
                  }
                });
                return;
             }
          }
        })
        .catch(err => {
          console.log(err);
        });


      }, 1000);
    },
    cancel(){
      this.$router.back(-1)
    }
  }
};
</script>

